"use client";

import { useEffect, useState } from "react";
import { motion, AnimatePresence } from "framer-motion";
import Link from "next/link";

const partners = [
  {
    name: "Partner A",
    slug: "partner-a",
    logos: [
      { src: "degzzo-logo-web.png", link: "/Partners/unique" },
      { src: "/logos/partner-a-2.svg", link: "/Partners/unique" },
    ],
    interval: 2000,
  },
  {
    name: "Partner B",
    slug: "partner-b",
    logos: [
      { src: "degzzo-logo-web.png", link: "/partners/unique" },
      { src: "/logos/partner-b-2.svg", link: "/partners/unique" },
    ],
    interval: 3000,
  },
  {
    name: "Partner C",
    slug: "partner-c",
    logos: [
      { src: "degzzo-logo-web.png", link: "/partners/unique" },
      { src: "/logos/partner-c-2.svg", link: "/partners/unique" },
    ],
    interval: 2500,
  },
  {
    name: "Partner D",
    slug: "partner-d",
    logos: [
      { src: "degzzo-logo-web.pngg", link: "/partners/partner-d/logo1" },
      { src: "/logos/partner-d-2.svg", link: "/partners/partner-d/logo2" },
    ],
    interval: 4000,
  },
  {
    name: "Partner E",
    slug: "partner-e",
    logos: [
      { src: "degzzo-logo-web.png", link: "/partners/partner-e/logo1" },
      { src: "/logos/partner-e-2.svg", link: "/partners/partner-e/logo2" },
    ],
    interval: 3500,
  },
  {
    name: "Partner F",
    slug: "partner-f",
    logos: [
      { src: "degzzo-logo-web.png", link: "/partners/partner-f/logo1" },
      { src: "/logos/partner-f-2.svg", link: "/partners/partner-f/logo2" },
    ],
    interval: 2200,
  },
];

export default function GlobalPartnerSection() {
  const [logoIndexes, setLogoIndexes] = useState(partners.map(() => 0));

  useEffect(() => {
    const intervals = partners.map((partner, i) =>
      setInterval(() => {
        setLogoIndexes((prev) => {
          const newIndexes = [...prev];
          newIndexes[i] = newIndexes[i] === 0 ? 1 : 0;
          return newIndexes;
        });
      }, partner.interval)
    );

    return () => intervals.forEach(clearInterval);
  }, []);

  return (
    <section className="bg-white py-16 px-4 md:px-12">
      <div className="max-w-6xl mx-auto text-center">
        <motion.h2
          className="text-3xl md:text-4xl font-bold mb-10"
          initial={{ opacity: 0, y: 30 }}
          whileInView={{ opacity: 1, y: 0 }}
          transition={{ duration: 0.6 }}
          viewport={{ once: true }}
        >
          Our Global Partners
        </motion.h2>

        <div className="grid grid-cols-2 sm:grid-cols-3 md:grid-cols-4 gap-10 items-start justify-center">
          {partners.map((partner, index) => {
            const activeLogo = partner.logos[logoIndexes[index]];
            return (
              <div key={index} className="text-center">
                {/* Logo with separate link */}
                <div className="flex justify-center h-20 relative mb-2">
                  <Link href={activeLogo.link}>
                    <AnimatePresence mode="wait">
                      <motion.img
                        key={activeLogo.src}
                        src={activeLogo.src}
                        alt={partner.name}
                        initial={{ opacity: 1 }}
                        animate={{ opacity: 1 }}
                        exit={{ opacity: 0 }}
                        transition={{ duration: 0.1 }}
                        className="absolute h-full object-contain grayscale hover:grayscale-0 transition duration-300 cursor-pointer"
                      />
                    </AnimatePresence>
                  </Link>
                </div>
              </div>
            );
          })}
        </div>
      </div>
    </section>
  );
}
